import Vue from "vue";
import Vuex from "vuex";
import axios from "axios";

Vue.use(Vuex);

const vrClassify = [
	{
		title: "西药1",
		class: 1
	},
	{
		title: "西药2",
		class: 1
	},
	{
		title: "西药3",
		class: 1
	},
	{
		title: "中药1",
		class: 2
	},
	{
		title: "中药2",
		class: 2
	},
	{
		title: "我的关注1",
		class: 3
	},
	{
		title: "我的关注2",
		class: 3
	}
];
const vrList = [
  {
    title: "资中特产"
  },
  {
    title: "东兴区特产"
  },
  {
    title: "市中区特产"
  }
];

export default new Vuex.Store({
  state: {
  	classify: vrClassify,
    list: vrList,
  	detail: null,
  	loading: false
  },
  mutations: {
  	GET_DETAIL_SUC (state, payload) {
  		state.detail = payload;
  	},
  	LOADING (state, payload) {
  		state.loading = payload;
  	},
  	CLEAR (state) {
  		state.detail = null;
  	},
    MOVEUP (state, payload) {
      state.list.splice(payload-1, 0, state.list.splice(payload, 1)[0]);
      console.log(JSON.stringify(state.list))
    },
    MOVEDOWN (state, payload) {
      let list = [...state.list];
      [list[payload+1], list[payload]] = [list[payload], list[payload+1]];
      state.list = list;
      console.log(JSON.stringify(state.list))
    }
  },
  actions: {
  	GET_DETAIL ({ commit }) {
  		commit('LOADING', true);
  		axios.get('http://neijiang.7typ.cn/msAutarchy/project/pay/b.php')
            .then(res => {
                commit('LOADING', false);
                commit('GET_DETAIL_SUC', res.data);
            })
            .catch(err => {
                console.log(err);
            })
  	}
  },
  getters: {
  	getClassify: ({classify}) => (c) => {
  		return classify.filter(v => {
  			if (c != 'all') {
  				return v.class == c;
  			} else {
  				return true;
  			}
  		})
  	}
  }
});